import { useState, useCallback } from 'react'

import { useDebounce } from '../../lib/useDebounce'

type UseTextFilterFieldReturnTuple = [
  text: string,
  setText: (newValue: string) => void,
  filterText: string,
  reset: () => void
]

export const useTextFilterField = (initialValue = ''): UseTextFilterFieldReturnTuple => {
  const [text, setText] = useState(initialValue)
  const [rawFilterText, setRawFilterText] = useDebounce(text, 500)

  const filterText = rawFilterText.trim()

  const reset = useCallback(() => {
    setText(initialValue)
    setRawFilterText(initialValue)
  }, [initialValue])

  return [text, setText, filterText, reset]
}
